import React,{useState} from "react";
import {useNavigate} from 'react-router-dom';
import styled from "styled-components";

import StyledMenuButton from "./StyledMenuButton";

const Wrapper = styled.div`
  width: 100vw;
  height: 3.5vh;
  background-color: #f6f6f6;
`;

const Menubar = styled.div`
  display: flex;
  position: relative;
  flex-direction: row;
  justify-content: left;
  align-items: flex-end;
  background-color: #f6f6f6;
  height: 3.6vh;
  padding: 0;
  margin: 0;
  z-index: 9;
  transition: 0.4s ease;
`;

function MenuButton(props) {
  const navigate = useNavigate();

  // xPosition 차이 값 (open&close)
  let xNum = 20;

  const onClickSearchCourse = () => {
    console.log("SearchCourse");
    navigate("/mayo-main/search-course");
  };
  const onClickPrevRegister = () => {
    console.log("PrevRegister");
    navigate("/mayo-main/prev-register");
  };
  const onClickRegister = () => {
    console.log("Register");
    navigate("/mayo-main/register");
  };
  const onClickMyRegister = () => {
    console.log("MyRegister");
    navigate("/mayo-main/my-register");
  };

  return (
    <Wrapper>
      <Menubar style={{ width: `${100 + props.xPosition}vw` }}>
        <StyledMenuButton
          buttonName="교과목신청"
          zIndex="40"
          onClick={onClickSearchCourse}
        />
        <StyledMenuButton
          buttonName="예비수강신청"
          zIndex="30"
          onClick={onClickPrevRegister}
        />
        <StyledMenuButton
          buttonName="수강신청"
          zIndex="20"
          onClick={onClickRegister}
        />
        <StyledMenuButton
          buttonName="수강내역조회"
          zIndex="10"
          onClick={onClickMyRegister}
        />
      </Menubar>
    </Wrapper>
  );
}

export default MenuButton;
